.twitter-body {
    background-image: url("https://debuild.co/images/UnicornVectorGradient_4.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    background-attachment: fixed;
  }
  
  .twitter-body-title {
    margin-left: 40%;
    text-align: left;
    padding-top: 10%;
  }
  
  .twitter-img {
    margin-left: 42%;
    width: 250px;
    height: 250px;
    padding: 20px;
    transition: width 1s, height 1s,transform 2s;
  }
  .twitter-img:hover {
      height: 300px;
      width: 300px;
      transform: rotate(360deg);
    
  }
  