.github-body {
  background-image: url("https://debuild.co/images/UnicornVectorGradient_4.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  background-attachment: fixed;
}

.github-body-title {
  margin-left: 45%;
  text-align: left;
  padding-top: 10%;
}

.gitimg {
  margin-left: 42%;
  width: 250px;
  height: 250px;
  padding: 20px;
  transition: width 1s, height 1s,transform 2s;
}
.gitimg:hover {
    height: 300px;
    width: 300px;
    transform: rotate(360deg);
  
}
