.social-body{
    background-image: url("https://debuild.co/images/UnicornVectorGradient_4.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    background-attachment: fixed;
    display: flex;
    padding: 15%;
}

.github-image{
    margin-left:20px;
    width: 250px;
    height: 250px;
    padding: 20px;
    transition: width 1s, height 1s,transform 2s;
  }
  .github-image:hover {
      height: 300px;
      width: 300px;
      transform: rotate(360deg);

}

.facebook-image{
