.home {
  background-image: url("https://debuild.co/images/UnicornVectorGradient_4.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  align-items: center;
  justify-content: center;
  background-attachment: fixed;
  
}

.home-body {
  padding: 5%;
  max-width: 100%;
  align-content: center;
  justify-content: center;
}

.home-body-title {
  font-size: 40px;
  max-width: 100%;
  padding-top: 20px;
}

.home-body-subtitle {
  font-size: 30px;
  max-width: 100%;
  padding-top: 5%;
}

.home-body-intro {
  font-size: 20px;
  max-width: 100%;
  padding-top: 5%;
}


.home-body-image {
  border-radius: 50%;
  height: 550px;
  width: 550px;
  padding: 15%;
}

.home-main-body {
  display: flex;
}

.home-body-content {
  margin-left: 5%;
  margin-top:-8%;
}

.home-body-list{
    display: flex;
    flex-direction: column;
    
}

.home-body-list > p {
    padding-top:20px;
}

@media screen and (max-width: 960px){
    .home{
        height: 100%;
    }
    .home-body{
        display: flex;
        flex-direction: column;
    }
}