.project-body {
    background-image: url("https://debuild.co/images/UnicornVectorGradient_4.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    background-attachment: fixed;
  }
  
  .project-body-title {
    margin-left: 38%;
    text-align: left;
    padding-top: 10%;
  }
  
  .project-img {
    margin-left: 42%;
    width: 250px;
    height: 250px;
    padding: 30px;
    transition: width 1s, height 1s,transform 2s;
  }
  .project-img:hover {
      height: 300px;
      width: 300px;
      transform: rotate(360deg);
    
  }
  
  @media screen and (max-width: 960px){
      .project-body-title{
          margin-left: 20%;
          padding-top:30%;
      }

      .project-img{
          margin-left: 35%;
          width:150px;
          height: 150px;
          padding: 20px;
          transition: width 1s, height 1s,transform 2s;
      }
      .project-img:hover{
          height: 200px;
          width: 200px;
          transform: rotate(360deg);
      }
  }